import React, { Profiler } from 'react';
import { useState } from 'react';
import Cookies from 'js-cookie';
import { Routes, Route } from 'react-router-dom';

import { Nav } from "./scripts/Nav";
import Profile from "./scripts/Profile/Profile";
import SiteError from "./scripts/SiteError/SiteError";
import { Box } from "./scripts/GameBox";
import Carousel from "./scripts/Carousel";
import Lander from "./scripts/Lander/Lander";
import { Slider } from "./scripts/Slider";

const getCookie = (cookie) => Cookies.get(cookie);
const serverURL = 'https://sock8-backend.onrender.com';
function Wrapper() {
    const [curruser, setCUser] = useState({
        id: parseInt(getCookie('userid')), token: getCookie('usertoken'), isguest: (/true/).test(getCookie('userisguest'))
    });
    const [mess, setMess] = useState({ open: false, msg: [""] });
    const [profile, setProfile] = useState({ open: false, user: "" });

    const initMess = async (msg, type = 'error') => {
		setTimeout(() => {
			setMess({ open: false, msg: [""], type })
		}, 3000);
		setMess({
			open: true,
			msg, type
		});
	}
    return (
        <Routes>
            <Route path='/' element={
                <>
                    <Nav profile={profile} setProfile={setProfile} shouldAuth={true} mess={mess} initMess={initMess} />
                     <Lander />
</>
            } />
            <Route path='/home' element={
                <>
                    <Nav profile={profile} setProfile={setProfile} mess={mess} initMess={initMess}/>
                    <Carousel />
                    <Slider />
                </>
            } />
            <Route path='/profile' element={
                <>
                    <Nav profile={profile} setProfile={setProfile} shouldAuth={false} mess={mess} initMess={initMess} />
                    <Profile profile={profile} mess={mess} initMess={initMess}/>
                </>
            } />
            <Route path='/error' element={
                <SiteError />
            } />
            <Route path='/auth/google' element={
                <>
                    {[0].map((ele) => {
                        if (window.location.pathname === '/auth/google') {
                            window.open(`${serverURL}${window.location.pathname}`, '_self')
                        }
                    })
                    }
                </>
            } />
            <Route path='/tic-tac-toe' element={
                <>
                    <Nav profile={profile} setProfile={setProfile} shouldAuth={false} mess={mess} initMess={initMess}/>
                    <Box user={profile.user} game='tictactoe' config={{ n: 3 }} value={'hello'} mess={mess} initMess={initMess} />
                </>
            } />
        </Routes>);
}

export default Wrapper;