import { useLayoutEffect, useRef, useState, useEffect } from "react";
import { findDOMNode } from "react-dom";
import { useSpring, useSpringRef, useTransition, animated, useChain } from '@react-spring/web';

import { ReactComponent as FullStar } from '../assets/FullStar.svg';
import { ReactComponent as HalfStar } from '../assets/HalfStar.svg';
import '../styles/carousel.css';

function Card(props) {
    const [currRot, setCurr] = useState(props.angle);

    const anim1 = useSpring({
        from: {
            border: '.3em solid #ffffff0a',
            boxShadow: '0'
        },
        to: [{
            border: `.2em solid ${props.game.cols[0]}`,
            boxShadow: `0 0 .2rem ${props.game.cols[0]}9F,0 0 .2rem ${props.game.cols[0]}9F,0 0 .2rem ${props.game.cols[0]}9F,0 0 0.1rem ${props.game.cols[0]}9F,0 0 0.5rem ${props.game.cols[0]}9F,inset 0 0 0.2rem ${props.game.cols[0]}9F`
        },
        {
            border: `.3em solid ${props.game.cols[0]}`,
            boxShadow: `0 0 .2rem ${props.game.cols[0]}9F,0 0 .2rem ${props.game.cols[0]}9F,0 0 .2rem ${props.game.cols[0]}9F,0 0 0.1rem ${props.game.cols[0]}9F,0 0 0.5rem ${props.game.cols[0]}9F,inset 0 0 0.2rem ${props.game.cols[0]}9F`
        },
        {
            border: `.3em solid #ffffffca`,
            boxShadow: `0 0 .2rem #fff,0 0 .2rem #fff,0 0 .2rem ${props.game.cols[0]},0 0 0.1rem ${props.game.cols[0]},0 0 0.5rem ${props.game.cols[0]},inset 0 0 0.2rem ${props.game.cols[0]}`
        },
        {
            border: `.3em solid white`,
            boxShadow: `0 0 .2rem #fff,0 0 .2rem #fff,0 0 2rem ${props.game.cols[0]},0 0 0.8rem ${props.game.cols[0]},0 0 2.8rem ${props.game.cols[0]},inset 0 0 1.3rem ${props.game.cols[0]}`
        }
        ],
        config: {
            duration: 800,
            mass: 5,
            friction: 120,
            tension: 120,
        }
    });

    if (!props.game) {
        return (<animated.div className='blankcard'>
            {1}
        </animated.div>);
    }
    else {
        return (
            <animated.div className={props.port ? 'vcard' : 'hcard'} style={anim1}>
                {() => { anim1.boxShadow.to(border => '0 0 .2rem #B001DF9F') }}
                <div className={`cardside ${props.game.released ? 'rel' : 'norel'}`} style={{border:`.3em solid ${props.game.cols[0]}`,boxShadow:`0 0 1em ${props.game.cols[0]},0 0 0.8rem ${props.game.cols[0]},0 0 1.8em ${props.game.cols[0]},inset 0 0 1.3rem ${props.game.cols[0]}`}}>
                    <img src={props.game.sshot}></img>
                    {props.game.sshot}
                    <ul>
                        {props.game.rating}/10
                        {[1, 2, 3, 4, 5].map((r) => {
                            if ((2 * r) <= props.game.rating) {
                                return (<li><FullStar /></li>);
                            }
                            else {
                                return (<li><HalfStar /></li>);
                            }
                        })}
                    </ul>
                </div>
                <article className={`${props.game.released ? 'rel' : 'norel'}`}>
                    <strong>{props.game.name}</strong><br></br>
                    {props.game.desc}

                </article>
            </animated.div>
        );
    }
}

export default Card;
