import React from 'react';
import Axios from "axios";
import Cookies from 'js-cookie';
import { useState, useEffect, useCallback } from "react";
import { useMediaQuery } from "react-responsive";
import { Box, IconButton, SvgIcon, TextField } from "@mui/material";
import Close from "@mui/icons-material/Close";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import PropTypes from 'prop-types';
import { deepPurple } from '@mui/material/colors';
import Carousel from '../Carousel';
import { ProfileWrapper } from './Profile.styled';
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectCoverflow, Pagination, Navigation } from 'swiper/modules';


import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
const Profile = ({
   profile, initMess, mess
}) => {
   const serverURL = 'https://sock8-backend.onrender.com';
   const [dis, setDis] = useState([true, true, true, true]);
   const [tab, setTab] = useState('Edit Profile');
   const [pic, setPic] = useState(profile.user.upic);
   const [enable, setEnable] = useState(false);
   const [user, setUser] = useState({ cuser: {}, cache: {} });

   const isPortrait = useMediaQuery({ query: '(max-width:990px)' });


   const unlock = (i) => {
      let bdis = [...dis];
      bdis[i] = false;
      setDis(bdis);
   }
   const lock = (i) => {
      let bdis = [...dis];
      bdis[i] = true;
      setDis(bdis);
   }
   const clearPic = (e)=>{
      setPic(profile.user.upic);
   }
   const check = (e) => {
      let nombre = (e.target.getAttribute("data-name"));
      let nuser = { ...user.cuser };
      nuser[nombre] = e.target.value;
      setUser({ cuser: nuser, cache: user.cache })
      if (Object.keys(user.cuser).some((el) => (nuser[el] != user.cache[el])) || profile.user.upic!=pic) {
         setEnable(true);
      } else {
         setEnable(false);
      }
   }
   const settab = (e) => {
      setTab(e.target.innerText);
   }
   const changePic = (e) => {
      let pic = e.target.files[0], mess = [], flag = false;
      let extension = (new RegExp(/\.([a-zA-Z]+)$/)).exec(pic.name);
      if (!extension || ['png', 'jpeg', 'jpg', 'gif', 'jfif','webp'].indexOf(extension[1]) < 0) {
         mess[0] = "Profile picture should be a jpeg,png or a gif only"; flag = true
      } else if (pic.size > (1024 * 1024)) {
         mess[0] = "Profile picture should be < 1MB"; flag = true
      }
      if (!flag) {
         let ourl = window.URL.createObjectURL(pic);
         setPic(ourl);
      }
      else {
         e.target.value = null;
         initMess(mess, 'err');
      }
   };
   const update = async () => {
      let formdata = new FormData(document.forms[0]);
      try {
         let req = await Axios.post(encodeURI(`${serverURL}/auth/modify?token=${Cookies.get('token')}`), formdata, {
            withCredentials: true,
            headers: {
               'Content-Type': 'multipart/form-data',
               'Access-Control-Allow-Credentials': true
            }

         });
         switch (req.status) {
            case 200:
               initMess(["Updated succesfully"], 'succ'); break;
            case 500:
               initMess(["Oops, something happened ..."]); break;
         }
      }
      catch (e) {
         if (e.response.status == 400) {
            if (e.response.data.msg) {
               initMess([e.response.data.msg], 'succ');
               // window.location.replace(window.location.href.split('?')[0]);
            }
            else {
               let errs = e.response.data.errors.map((err) => err.msg);
               initMess(errs);
            }
         }
      }
   }
   const fetchData = async () => {
      let getProfile = await fetch(serverURL + `/auth/getData?token=${Cookies.get('token')}`, {
         credentials: "include",
         headers: {
            "Content-Type": "application/json",
            'Access-Control-Allow-Credentials': true
         }
      });
      let nprofile = await getProfile.json();
      if (getProfile.status === 200) {
         setUser({ cuser: nprofile.user, cache: nprofile.user });
      } else {
         initMess(["Couldn't fetch details ..."]);
      }
   }
   useEffect(() => {
      fetchData();
      if (window.location.href.includes("?upd=true")) {
         initMess(["Successfully updated"], 'succ')
      }
      window.URL = window.URL || window.webkitURL;
      setPic(profile.user.upic);
   }, []);
   useEffect(() => {
      if(pic!=profile.user.upic){
         setEnable(true);
      }else{setEnable(false)}
   }, [pic]);
   return (
      <ProfileWrapper id="profilePage" style={{ display: "grid", placeItems: "center", width: "100vw", height: "80vh" }}>
         <Box id="Profile" style={{ width: "80%", aspectRatio: (isPortrait ? "calc(1.08 / 1.08)" : "calc(6 / 1.08)") }}>
            <div id="profileBanner" style={{}}>
               {
                  ['Edit Profile', 'Gaming history', 'Friends'].map((el) => (<span className={tab == el ? "active" : ""} onClick={settab}>{el}</span>))
               }
            </div>
            {
               [0].map((el) => {
                  if (tab == 'Edit Profile') {
                     return (<>
                        <span id="updpic">
                           <img id="profilePic" sx={{}} src={pic} size="small"></img>
                           <input name="modpic" type="file" form="upd" onInput={changePic} onMouseOver={(e) => { e.target.style.background = `url('../cam.svg') 50% no-repeat,#00000034` }} onMouseLeave={(e) => { e.target.style.background = `none` }} style={{}}></input>
                           <IconButton variant="outline" color="error" style={{opacity:((pic!=profile.user.upic)?1:0),position:"absolute",outline:".4rem solid #ddd",height:'30px',width:'30px',transform:'translate(365%,-200%)'}} onClick={clearPic}>
                              <Close />
                           </IconButton>
                        </span>
                        <span style={{ width: "80%" }}>
                           <form name="update" action={serverURL + "/auth/modify"} method="POST" id="upd" style={{ display: "flex" }}>
                              <fieldset id="profileBoard" style={isPortrait ? { display: "flex", flexDirection: "column", alignItems: "baseline" } : { display: "grid", gridTemplateColumns: "repeat(3,14vw)", width: "42vw", columnGap: "20%" }}>
                                 <span><label>Name</label><TextField onInput={check} inputProps={{ "data-name": "name", readOnly: dis[0] }} sx={{ '& .MuiOutlinedInput-root': { "&.Mui-focused": { background: "#00000036" }, "& .MuiOutlinedInput-notchedOutline": { borderColor: "transparent" } }, input: { color: 'white', borderColor: 'none' } }} size="small" name="modname" onBlur={lock.bind(null, 0)} onDoubleClick={unlock.bind(null, 0)} type="text" value={user.cuser.name}></TextField></span>
                                 <span><label>Username</label><TextField onInput={check} inputProps={{ "data-name": "uname", readOnly: dis[1] }} sx={{ '& .MuiOutlinedInput-root': { "& .MuiOutlinedInput-notchedOutline": { borderColor: "transparent" } }, input: { color: 'white', border: 'none' } }} size="small" name="moduname" onBlur={lock.bind(null, 1)} onDoubleClick={unlock.bind(null, 1)} type="text" value={user.cuser.uname}></TextField></span>
                                 <span><label>Phone number</label><TextField onInput={check} inputProps={{ "data-name": "phone", readOnly: dis[2] }} sx={{ '& .MuiOutlinedInput-root': { "& .MuiOutlinedInput-notchedOutline": { borderColor: "transparent" } }, input: { color: 'white', border: 'none' } }} size="small" name="modphone" onBlur={lock.bind(null, 2)} onDoubleClick={unlock.bind(null, 2)} type="number" value={user.cuser.phone}></TextField></span>
                                 <span>
                                    <label>Password</label><TextField onInput={check} inputProps={{ "data-name": "passwd", readOnly: dis[3] }} sx={{ '& .MuiOutlinedInput-root': { "& .MuiOutlinedInput-notchedOutline": { borderColor: "transparent" } }, input: { color: 'white', border: 'none' } }} size="small" name="modpwd" onBlur={lock.bind(null, 3)} onDoubleClick={unlock.bind(null, 3)} type="password" value={user.cuser.passwd}></TextField></span>
                              </fieldset>
                           </form>
                        </span>
                        <span style={{ display: "flex", width: "100%", justifyContent: "end" }}>
                           <IconButton form="upd" onClick={update} sx={{ borderRadius: "50%", width: "40px", margin: ".8rem", aspectRatio: "1.0", background: "white", transform: "scale(2)" }} disabled={!enable} variant="contained" color="error">
                              <SvgIcon>
                                 <svg xmlns="http://www.w3.org/2000/svg" width="50px" height="50px" viewBox="0 0 24 24"><path fill="currentColor" d="m11 11.85l-1.875 1.875q-.3.3-.712.288T7.7 13.7q-.275-.3-.288-.7t.288-.7l3.6-3.6q.15-.15.325-.212T12 8.425t.375.063t.325.212l3.6 3.6q.3.3.288.7t-.288.7q-.3.3-.712.313t-.713-.288L13 11.85V19q0 .425-.288.713T12 20t-.712-.288T11 19zM4 8V6q0-.825.588-1.412T6 4h12q.825 0 1.413.588T20 6v2q0 .425-.288.713T19 9t-.712-.288T18 8V6H6v2q0 .425-.288.713T5 9t-.712-.288T4 8" /></svg>
                              </SvgIcon>
                           </IconButton>
                        </span>
                     </>
                     );
                  }
                  else if (tab == 'Gaming history') {
                     return (
                        <Swiper
                           style={{ height: 'min(400px,80vh)', width: '40vw', overflow: "hidden" }}
                           grabCursor={true}
                           slideShadows={false}
                           centeredSlides={true}
                           loop={false}
                           slidesPerView={'auto'}
                           spaceBetween={50}
                           pagination={{ el: '.swiper-pagination', clickable: true }}
                           navigation={{
                              nextEl: '.swiper-button-next',
                              prevEl: '.swiper-button-prev',
                              clickable: true,
                           }}
                           modules={[Pagination, Navigation]}
                           className="swiper_container"
                        >
                           {[0, 1, 2, 3].map((game, i) => (
                              <SwiperSlide id={i}>
                                 <div style={{ background: "white", height: "100%" }}>123</div>
                              </SwiperSlide>
                           ))}

                        </Swiper>
                     );

                  }
               })
            }

         </Box>
      </ProfileWrapper>
   );
}

Profile.propTypes = {};

Profile.defaultProps = {};

export default Profile;
