import "../styles/Nav.scss";
import Axios from "axios";
import Cookies from 'js-cookie';
import logo from "../assets/logo (1).svg";
import { useState, useEffect, useCallback } from "react";
import { useMediaQuery } from "react-responsive";
import EditIcon from '@mui/icons-material/Edit';
import Box from "@mui/material/Box";
import Switch from "@mui/material/Switch";
import Modal from "@mui/material/Modal";
import Button from "@mui/material/Button";
import CloseIcon from "@mui/icons-material/Close";
import IconButton from "@mui/material/IconButton";
import SearchIcon from "@mui/icons-material/Search";
import TextField from "@mui/material/TextField";
import Avatar from '@mui/material/Avatar';
import { ThemeProvider, createTheme } from "@mui/material/styles";
import { deepPurple } from '@mui/material/colors';

const darkTheme = createTheme({
	palette: {
		mode: "dark",
	},
});

const authStyleLandscape = {
	position: 'absolute',
	top: '50%', left: '50%',
	transform: 'translate(-50%,-50%)',
	width: 'max(300px,60vw)',
	aspectRatio: 'calc(1.96 / 1.08)',
	bgcolor: 'white',
	border: '5px solid gray',
	p: 4
};
const authStylePortrait = {
	scrollSnapBehavior: '',
	position: 'absolute',
	top: '50%', left: '50%',
	transform: 'translate(-50%,-50%)',
	width: 'max(300px,80vw)',
	aspectRatio: 'calc(1.08 / 1.96)',
	bgcolor: 'white',
	border: '5px solid gray',
	p: 4
};
const Oauth = ({ type }) => {
	if (type == 'login') {
		return (
			<>
				<span id="oauth">
					<Button sx={{ background: "white", borderRadius: '2rem' }} onClick={() => { window.location.replace("./auth/google") }}>
						<img src={`${window.location.origin}/google.png`} height={50} width={50}></img>Google
					</Button>
					<Button sx={{ background: "white", borderRadius: '2rem' }} onClick={() => { window.location.replace("./auth/fb") }}>
						<img src={`${window.location.origin}/fb.png`} height={50} width={50}></img>FB
					</Button>
				</span>
			</>
		);
	}
	else {
		return (
			<>
				<span id="oauth">
					<Button sx={{ background: "#272727", borderRadius: '2rem' }} onClick={() => { window.location.replace("./auth/google") }}>
						<img src='./google.png' height={50} width={50}></img>Google
					</Button>
					<Button sx={{ background: "#272727", borderRadius: '2rem' }} onClick={() => { window.location.replace("./auth/fb") }}>
						<img src='./fb.png' height={50} width={50}></img>FB
					</Button>
				</span>
			</>
		);
	}
}
const serverURL = 'https://sock8-backend.onrender.com';
export function Nav({ profile, setProfile, shouldAuth, initMess, mess }) {
	const [SearchQuery, setSearchQuery] = useState("");
	const [status, setStatus] = useState("");
	const [menu, setMenu] = useState(false);
	const [authOpen, setAuthOpen] = useState(false);
	const [authType, setAuthType] = useState('login');
	const [isGoogle, setGoogle] = useState(false);
	const openclose = (open = false) => {
		setAuthOpen(open);
	}
	const handleStatus = (e) => {
		setStatus(e.target.value);
	}
	const auth = async () => {
		let getProfile = await fetch(serverURL + `/auth?token=${Cookies.get('token')}`, {
			credentials: "include",
			headers: {
				"Content-Type": "application/json",
				'Access-Control-Allow-Credentials': true
			}
		});
		let nprofile = await getProfile.json();
		if (getProfile.status === 200) {
			setProfile({ open: true, user: nprofile.user });
		} else {
			if (window.location.pathname.includes("/profile")) {
				window.location.replace('/error');
			}
			console.log(nprofile);
			initMess(["You are not authenticated ..."], 'succ');
			setProfile({ open: false, user: {} });
		}
	}

	const login = async () => {
		let form = document.forms['authenticate'];
		try {
			form.submit();
		} catch (e) {
			console.log(e);
		}
	}
	const logout = () => {
		Cookies.remove('token'); Cookies.set('user', JSON.stringify({}));
		window.location.replace(`${serverURL}/auth/logout`);
	}
	const signUp = async () => {
		let form = document.forms['authenticate'];
		let eles = Array.from(form.elements).filter((ele) => (ele.name.startsWith('sign')));
		let signupData = {};
		eles.forEach((ele) => {
			let nd = {}; nd[ele.name] = ele.value;
			Object.assign(signupData, nd);
		});
		try {
			let req = await Axios.post(encodeURI(`https://sock8-backend.onrender.com/auth/signuplocal${isGoogle ? "?isGoogle=true" : ""}`), signupData, {
				'Content-Type': 'application/json'
			});
			switch (req.status) {
				case 200:
					initMess(["Check your mail for verification"], 'succ'); break;
				case 500:
					initMess(["Oops, something happened ..."]); break;
			}
		}
		catch (e) {
			if (e.response.status == 400) {
				if (e.response.data.status == "reload") {
					window.location.replace(window.location.href.split('?')[0]);
				}
				else {
					let errs = e.response.data.errors.map((err) => err.msg);
					initMess(errs);
				}
			}
		}
	}
	const verify = async (key) => {
		try {
			let req = await Axios.get(encodeURI(`https://sock8-backend.onrender.com/auth/verify?key=` + key), {
				'Content-Type': 'application/json'
			});
			switch (req.status) {
				case 200:
					// Exchange login credentials here for oauth
					window.location.replace('/?signup=true');
				case 500:
					initMess(["Oops, something happened ..."]); break;
			}
		}
		catch (e) {
			console.log(e);
		}
	}
	const toggleAuth = (e) => {
		if (isPortrait) {
			let af = document.querySelector("#authForms form");
			e.target.checked ? af.scrollTo(af.offsetWidth, 0) : af.scrollTo(0, 0);
		}
		document.querySelector("#authForms").scrollTo(0, 0);
		e.target.checked ? setAuthType('signup') : setAuthType('login');
	}

	useEffect(() => {
		const url = new URL(window.location.href);
		const params = url.searchParams;
		auth();

		if (params.get('key')) {
			verify(params.get('key'));
		}
		if (params.get('signup') == 'true') {
			initMess(["Successfully signed up ..."], 'succ');
		}
		else if (params.get('login')) {
			if (params.get('login') == 'true') {
				initMess(["Successfully logged in ..."], 'succ');
			}
			else {
				initMess(["Couldn't log in ..."]);
			}
		}
		if (params.get('signupgoogle') == 'true' && params.get('token')) {
			 ( async () => {
				setGoogle(true);
				let getProfile = await fetch(serverURL + `/auth/temp?token=${Cookies.get('token')}`, {
					credentials: "include",
					headers: {
						"Content-Type": "application/json",
						'Access-Control-Allow-Credentials': true
					}
				});
				let nprofile = await getProfile.json();
				if (getProfile.status === 200) {
					setProfile({ open: true, user: nprofile.user });
				} else {
					initMess(["You are not authenticated ..."]);
					setProfile({ open: false, user: {} });
				}
				setAuthOpen(true);
			})()
		}
		if (params.get('signupgoogle') == 'true') {
			(() => {
				setAuthOpen(true);
				setGoogle(true);
			})()
		}
		else if (params.get('token')) {
			Cookies.set('token', params.get('token'));
			window.location.replace(window.location.origin + window.location.pathname);
		}


	}, []);
	useEffect(() => {
		if (!isGoogle) {
			Cookies.set("user", JSON.stringify(profile.user), {
				expires: 2
			});
		}
	}, [profile.user]);
	const isPortrait = useMediaQuery({ query: '(max-width:500px)' });
	return (
		<nav>
			<div id="brand">
				<span>S
					<img src={logo} alt="o"></img>
					ck8's</span>
			</div>
			<div id="nav-control">
				<ThemeProvider theme={darkTheme}>
					<TextField
						id="search-bar"
						className="text"
						onInput={(e) => {
							setSearchQuery(e.target.value);
						}}
						label="Search games"
						variant="outlined"
						color="secondary"
						size="small"
						value={SearchQuery}
					/>
					<IconButton type="submit" aria-label="search">
						<SearchIcon style={{ fill: "hsl(300, 40%, 8	0%)" }} />
					</IconButton>
				</ThemeProvider>
				{
					[1].map((ele) => {
						if (!Cookies.get('Auth-Cookie')) {
							return (
								<>
									{
										[0].map((ele) => {
											if (profile.open) {
												if (profile.user.upic) {
													return <img onMouseOver={() => { setMenu(true) }} src={profile.user.upic} id="ppic"></img>;
												}
												else if (profile.user.uname) {
													return <strong onMouseOver={() => { setMenu(true) }} style={{ color: "white" }}>{profile.user.uname}</strong>;
												}
											} else {
												return <Button onClick={openclose} variant="outlined">Login</Button>;
											}
										})
									}
									<Box id="pmenu" onMouseLeave={() => { setMenu(false) }} sx={{ display: (menu) ? "flex" : "none" }}>
										<IconButton sx={{
											position: 'absolute',
											right: 0,
											top: 8,
											color: (theme) => (authType == 'login' ? 'white' : theme.palette.grey[500]),
										}} onClick={() => { window.location.replace("./profile/") }}>
											<EditIcon />
										</IconButton>
										<span><strong> Username </strong>{profile.user.uname}</span>
										<span><strong> Email address</strong> {profile.user.mail}</span>
										<Button onClick={logout} variant="contained" color="error">Logout</Button>
									</Box>
									<Modal id="authSign" open={mess.open} onClose={openclose.bind(null, true)}>
										<Box id="messbox" sx={{ position: 'absolute', transition: "all .8s ease-in", display: "grid", placeItems: "center", top: '10%', left: '40%', borderRadius: "2rem", aspectRatio: 'calc(4.96 / 1.08)', bgcolor: (mess.type === 'error' ? '#dc6601c3' : '#ddd'), p: 4 }}>
											<div style={{ display: "flex", flexDirection: "column", gap: ".6rem", color: (mess.type === 'error' ? 'white' : '#272727'), fontFamily: 'bahnschrift', fontSize: '1.3rem' }}>
												{mess.msg.map((ms) => <span>{ms}</span>)}
											</div>
										</Box>
									</Modal>
									<Modal id="authSign" open={authOpen} onClose={openclose.bind(null, true)}>
										<Box id="authBox" sx={(isPortrait ? authStylePortrait : authStyleLandscape)} style={{ background: (authType != 'login' ? 'white' : '#272727') }}>
											<div id="authHeader" style={{ color: (authType == 'login' ? 'white' : '#272727') }}>

												<h4>{isGoogle ? "Fill in your details ..." : (authType == 'login' ? "Login" : "Sign up")}</h4>
												{
													[0].map((ele) => {
														if (!isGoogle) {
															return <>
																<Switch id="toggle" color="secondary" onClick={toggleAuth}></Switch>
																<IconButton
																	aria-label="close"
																	onClick={openclose.bind(null, false)}
																	sx={{
																		position: 'absolute',
																		right: 8,
																		top: 8,
																		color: (theme) => (authType == 'login' ? 'white' : theme.palette.grey[500]),
																	}}
																>
																	<CloseIcon />
																</IconButton>
															</>
														}
													})
												}

											</div>
											<div id="authForms" style={{ boxShadow: `inset -2px -8px 10px ${(authType == 'login') ? 'white' : '#272727'}`, overflow: ((authType == 'signup' || isGoogle) ? "scroll" : "hidden") }}>
												<form name="authenticate" action={`${serverURL}/auth/${authType}local`} method="POST">
													<div id="banner" style={{ background: (authType == 'login' ? 'white' : '#272727'), transform: (authType == 'login' ? "translateX(86%)" : "translateX(0%") }}>
														<span>
															<img src={logo} alt="o"></img>
														</span>
													</div>
													<fieldset className="authFields login" style={{ display: (isGoogle ? "none" : "flex") }}>
														<span><label>Name</label><TextField size="small" name="logname" type="text" required></TextField></span>
														<span><label>Password</label><TextField size="small" name="logpwd" type="text"></TextField></span>
														<Button onClick={login} variant="contained" color="warning">Login</Button>
														<Oauth />
													</fieldset>
													<fieldset className="authFields signup" >
														{
															[0].map((ele) => {
																if (!profile.user.upic || profile.user.upic == '') {
																	return <Oauth />
																} else {
																	return <span style={{ background: '#272727', borderTopLeftRadius: '1.3rem', borderTopRightRadius: '1.3rem' }}><img src={profile.user.upic} style={{ borderRadius: '50%', height: '100px', aspectRatio: '1.0', border: ".3rem solid white" }}></img></span>
																}
															})
														}
														{
															[0].map((ele) => {
																if (isGoogle && profile.user.name) {
																	return (<><span><label>First Name</label><TextField disabled={isGoogle} sx={{ input: { color: 'white', background: (isGoogle ? '#708090a4' : '#272727') } }} size="small" name="signfname" type="text" value={(profile.user.uname.split(' ')[0])}></TextField></span>
																		<span><label>Last Name</label><TextField disabled={isGoogle} sx={{ input: { color: 'white', background: (isGoogle ? '#708090a4' : '#272727') } }} size="small" name="signlname" type="text" value={(profile.user.uname.split(' ')[1])}></TextField></span></>);
																} else {
																	return (<>
																		<span><label>First Name</label><TextField disabled={isGoogle} sx={{ input: { color: 'white', background: (isGoogle ? '#708090a4' : '#272727') } }} size="small" name="signfname" type="text"></TextField></span>
																		<span><label>Last Name</label><TextField disabled={isGoogle} sx={{ input: { color: 'white', background: (isGoogle ? '#708090a4' : '#272727') } }} size="small" name="signlname" type="text"></TextField></span>
																	</>);
																}
															})
														}
														<span><label>Username</label><TextField sx={{ input: { color: 'white' } }} size="small" name="signuname" type="text"></TextField></span>
														{
															[0].map((ele) => {
																if (isGoogle && profile.user.mail) {
																	return (
																		<span><label>Email-id</label><TextField disabled={isGoogle} sx={{ input: { color: 'white', background: (isGoogle ? '#708090a4' : '#272727') } }} size="small" name="signmail" type="email" value={profile.user.mail}></TextField></span>
																	);
																} else {
																	return (
																		<span><label>Email-id</label><TextField disabled={isGoogle} sx={{ input: { color: 'white', background: (isGoogle ? '#708090a4' : '#272727') } }} size="small" name="signmail" type="email" ></TextField></span>
																	);
																}
															})
														}
														<span><label>Phone number</label><TextField sx={{ input: { color: 'white' } }} size="small" name="signphone" type="number"></TextField></span>
														<span>
															<label>Password</label><TextField sx={{ input: { color: 'white' } }} size="small" name="signpwd" type="text"></TextField></span>
														<span><label>Confirm Password</label><TextField sx={{ input: { color: 'white' } }} size="small" name="signpwd2" type="text"></TextField></span>
														<span><Button onClick={signUp} variant="contained" color="warning">Sign up</Button>
														</span>
													</fieldset>
												</form></div>

										</Box>
									</Modal>
								</>
							)
						}
						else {
							<Avatar sx={{ bgcolor: deepPurple[400] }} alt="Ravish Ranjan" className="avatar pixel-font">Rr</Avatar>
						}
					})
				}
			</div>
		</nav>
	);
}
