
import { useState, useEffect, useCallback } from 'react';
import Cookies from 'js-cookie';
import "../../styles/tictactoe.css";
import { useSpring,animated } from "@react-spring/web";
import { Button } from '@mui/material';
import socket from "../socket";

function TicTacToe(props) {
	let historyCache = JSON.parse(Cookies.get('gameHistory')).history;

	const [board, setBoard] = useState((new Array(props.config.n)).fill([]));
	const [move, setMove] = useState((historyCache.length != 0) ? (props.details.move) : (props.details.mark == "x" ? 0 : 1));
	const [toResult, api] = useSpring(() => ({
		background: props.details.color, color: 'white',
		border: props.details.color,
		fontFamily: 'Mukta,Bahnschrift', fontStyle: 'normal', fontWeight: '800',
		aspectRatio: '1', overflow: 'hidden',
		placeContent: 'center', textAlign: 'center',
		height: '100%', fontSize: '1000% !important'
	}));
	useEffect(() => {
		let nboard = board.map((row) => { return Object.values({ ...row }) });
		for (var i = 0; i < props.config.n; i++) {
			nboard[i] = (new Array(props.config.n)).fill(-1);
		}
		if (historyCache.length > 0) {
			let mymark = (props.details.mark == "x" ? 0 : 1);
			historyCache.forEach((iin, i) => {
				nboard[Math.floor(iin / props.config.n)][iin % props.config.n] = ((i % 2 == mymark) ? 1 : 0);
			});
		}
		setBoard(nboard);
		console.log(props.details);
	}, []);
	useEffect(() => {
		if (props.status.cond != 'pending') {
			api.start({
				background: props.details.color, color: '#ddd',textShadow:`0px 0px 10px white`,
				outline: `.1rem solid ${props.details.color}`,boxShadow: `0 0 4px white,0 0 3px white,0 0 20px ${props.details.color},0 0 20px ${props.details.color},0 0 20px ${props.details.color}`,
				immediate:true
			})
		}
	}, [props.status.cond]);
	useEffect(() => {
		let newdetails = { ...props.isConnected };
		newdetails.details.move = move;
		props.setConnected(newdetails);
		if (move === 1) {
			moveListener();
			socket.removeAllListeners("MOVACK");
		}
		else {
			socket.removeAllListeners('move');
		}
	}, [move]);
	const moveListener = useCallback(() => {
		console.log(board);
		if (move === 1) {
			let nb = board.map((row) => { return Object.values({ ...row }) });
			if (!isFinite(nb[0][0])) {
				for (var i = 0; i < props.config.n; i++) {
					nb[i] = (new Array(props.config.n)).fill(-1);
				}
				let mymark = (props.details.mark == "x" ? 0 : 1);
				historyCache.forEach((iin, i) => {
					nb[Math.floor(iin / props.config.n)][iin % props.config.n] = ((i % 2 == mymark) ? 1 : 0);
				});
			}
			socket.on('move', (newmove) => {
				console.log(newmove.from);
				if (newmove.from !== props.user.uname && move == 1) {
					// Check for victory or loss here
					nb[Math.floor(newmove.move.index / props.config.n)][newmove.move.index % props.config.n] = 0;
					socket.emit("MOVACK", true);
					props.setHistory([...props.history].concat([newmove.move.index]));
					setBoard(nb);
					setMove((move + 1) % 2);
				}
			});

		}
	}, [board, move]);
	const setMark = useCallback((ind) => {
		if (move === 0) {
			if (board[Math.floor(ind / props.config.n)][ind % props.config.n] != -1) {
				props.initMess(['Illegal move'], 'err');
				// window.alert('Illegal move');
				return;
			}
			socket.emit('move', {
				from: props.user.uname,
				move: {
					index: ind
				}
			});
			socket.on("MOVACK", (tt) => {
				// Settimeout for move here
				let nb = board.map((row) => { return Object.values({ ...row }) });
				nb[Math.floor(ind / props.config.n)][ind % props.config.n] = 1;
				setBoard(nb);
				setMove((move + 1) % 2);
				props.setHistory([...props.history].concat([ind]));
				socket.removeAllListeners("MOVACK");
			})
		}
		else {
			props.initMess(["Not your move"]);
			// window.alert('Not your move!!!');
		}
	}, [board]);
	useEffect(() => {
		//  Loser-centric approach:- i.e. Loser poses the whiteflag
		board.slice().flat().forEach((eq) => {
			if (!isFinite(eq) || !Math.abs(eq)) {
				return;
			}
		});
		console.log(`Starting iteration`);
		let conds = {
			wflag: 'none', ind: -1
		};
		let nboard = board.map((row) => { return Object.values({ ...row }) });
		nboard.forEach(function (row, i) {
			let vals = { flag: true };
			nboard[i].map(function (sq, i) {
				if (sq != 0 && this.flag) {
					this.flag = false;
				}
			}, vals);
			if (vals.flag) {
				this.wflag = 'row'; this.ind = i;
			}
		}, conds);
		nboard = board.map((row) => { return Object.values({ ...row }) });
		let vals = { dflag: true, cflag: true };
		vals.dflag = true;
		let str = '';
		nboard.forEach(function (el, i) {
			// str += `${i},${i}:${nboard[i][i]}:${(this.dflag) ? 'true' : 'false'}`;
			if (nboard[i][i] != 0) {
				this.dflag = false;
			}
		}, vals);
		console.log(`==>${str}<==`)
		if (vals.dflag) {
			conds.wflag = 'diag'; conds.ind = 0;
		}

		vals.dflag = true;
		nboard = nboard.map((row) => row.reverse());
		(new Array(nboard[0].length).fill(0)).forEach(function (z, i) {
			if (nboard[i][i] != 0) {
				this.dflag = false;
			}
		}, vals);
		if (vals.dflag) { conds.wflag = 'diag'; conds.ind = 1; }
		nboard = nboard.map((row) => row.reverse());
		for (var i = 0; i < nboard.length; i++) {
			for (var j = 0; j < nboard[0].length; j++) {
				nboard[i][j] = board[j][i];
			}
		}
		nboard.forEach(function (col, i) {
			let vals = { flag: true };
			nboard[i].map(function (sq, i) {
				if (sq != 0 && this.flag) {
					this.flag = false;
				}
			}, vals);
			if (vals.flag) {
				this.wflag = 'col'; this.ind = i;
			}
		}, conds);

		let iind = [];
		if (conds.wflag == 'row') {
			nboard[0].forEach((e, i) => {
				iind.push((nboard[0].length * conds.ind) + i);
			});
		}
		else if (conds.wflag == 'col') {
			nboard[0].forEach((e, i) => {
				iind.push(conds.ind + (nboard[0].length * i));
			});
		}
		else if (conds.wflag == 'diag') {
			if (conds.ind == 0) {
				nboard[0].forEach((e, i) => {
					iind.push((i * nboard[0].length) + i);
				});
			}
			else if (conds.ind == 1) {
				nboard[0].forEach((e, i) => {
					iind.push((nboard[0].length - 1) * (i + 1));
				});
			}
		}
		if (conds.wflag != 'none') {
			if (!isFinite(iind[0])) { return; }
			socket.emit('LOSS', {
				id: props.user.uname,
				indices: iind
			});
			socket.on('LOSSACK', (d) => {
				props.setStatus({ cond: 'loss', indices:props.status.indices,critical:iind, winner: [d.id], loser: [props.user.uname] });
			});
		}
		else {
			let opts = { full: true };
			board.flat().forEach(function (ele) {
				console.log(`**${ele}`)
				if (ele < 0) {
					opts.full = false;
				}
			}, opts);
			if (opts.full) {
				socket.emit('DRAW', {
					id: props.user.uname
				});
				socket.on('DRAWACK', (d) => {
					props.setStatus({ ...props.status, cond: 'draw' });
				});
			}
		}
	}, [board]);
	useEffect(() => {
		if (props.status.cond == 'pending') {
			socket.on('LOSS', (obj) => {
				// window.alert(`${obj.id} lost`);
				console.log(`$--------->${obj.indices}`)
				socket.emit('LOSSACK', { id: props.user.uname });
				props.setStatus({ cond: 'win',indices:props.status.indices, critical: obj.indices, winner: [props.user.uname], loser: [obj.id] });
			});
			socket.on('DRAW', (obj) => {
				// window.alert(`${obj.id} lost`);
				socket.emit('DRAWACK', { id: props.user.uname });
				props.setStatus({ ...props.status, cond: 'draw' });
			});
		}
		else {
			socket.removeAllListeners('LOSSACK');
			socket.removeAllListeners('LOSS');
			socket.removeAllListeners('DRAWACK');
			socket.removeAllListeners('DRAW');
			props.saveG(props.status.winner.concat(props.status.loser).filter((ele) => ele != props.user.uname), props.status.cond, [])
		}
	}, [props.status.cond]);
	return (
		<div
			className="TicTacToe game"
			style={{
				aspectRatio: props.orientation === "vertical" ? "3/4" : "4/3"
			}}>
			<div className="board" style={{ gridTemplateColumns: `repeat(${props.config.n},1fr)`, gridTemplateRows: `repeat(${props.config.n},1fr)` }}>
				{
					Array(Math.pow(props.config.n, 2)).fill(0).map((square, i) =>
						<animated.span key={i} style={isFinite(props.status.critical.find((ind) => ind === i)) ? toResult : {
							filter: (props.status.cond != 'pending' ? '' : (move == 1 ? 'brightness(40%)' : '')),
							boxShadow: ((props.status.cond=='pending')?`0 0 4px white,0 0 3px white,0 0 20px ${props.details.color},0 0 20px ${props.details.color},0 0 20px ${props.details.color}`:''), height: '100%', borderTop: `solid ${(Math.floor(i / props.config.n) == 0) ? 1 : 0}rem ${(Math.floor(i / props.config.n) == 0) ? 'white' : '#ffffff00'}`, borderBottom: `solid ${(Math.floor(i / props.config.n) == (props.config.n - 1)) ? 1 : 0}rem ${(Math.floor((i + 1) / props.config.n) == 0) ? 'white' : '#ffffff00'}`, borderLeft: `solid ${((i % props.config.n) == 0) ? 1 : 0}rem ${((i % props.config.n) == 0) ? 'white' : '#ffffff00'}`, borderRight: `solid ${(((i + 1) % props.config.n) == 0) ? 1 : 0}rem ${(((i + 1) % props.config.n) == 0) ? 'white' : '#ffffff00'}`, fontSize: '5rem', borderColor: 'white', color: ((board[Math.floor(i / props.config.n)][i % props.config.n] == 1) ? props.details.color : 'white')}}  onClick={(props.status.cond == 'pending') ? setMark.bind(null, i) : null}>
							{
								(() => {
									switch (board[Math.floor(i / props.config.n)][i % props.config.n]) {
										case -1:
											return ' '; break;
										case 1:
											return props.details.mark; break;
										case 0:
											return props.details.mark == "o" ? "x" : "o";
									}

								})()
							}

						</animated.span>
					)
				}
			</div>
			{(props.status.cond == 'pending') ? ((move == 1) ? 'Their move' : 'Your move') : ''}
			{(props.status.cond == 'win') ? `You won against ${props.status.loser.join(',')}` : ((props.status.cond == 'loss') ? `You lost against ${props.status.winner.join(',')}` : (props.status.cond == 'draw') ? 'You drew' : '')}
		</div>
	);
};
export default TicTacToe; 
