import "../styles/RecentRow.scss";
import { useState, useEffect, useCallback,useContext ,createContext,createElement} from "react";
import Cookies from 'js-cookie';
import { Card, CardContent, CardMedia, Typography, Button, IconButton, LinearProgress, Select, MenuItem, FormControl } from "@mui/material";
import {CloseIcon,ReloadIcon,Play} from "./Icons";
import axios from "axios";
import socket from "./socket";
import { GamesContext } from "./GamesContext";


// Try useCallback for caching the above method
const REQ_TIMEOUT = 900;
export function Box(props) {
	const gc = useContext(GamesContext);
	const [isConnected, setConnected] = useState({ status: Cookies.get('connectStatus'), roomns: Cookies.get("socket.roomid"),details:(Cookies.get('gameDetails') && JSON.parse(Cookies.get('gameDetails')) || '') });
	const [timer, setTimer] = useState(Cookies.get('connectTimer') ? Cookies.get('connectTimer') : 0);
	const [history,setHistory] = useState(Cookies.get('gameHistory')?JSON.parse(Cookies.get('gameHistory')).history:[]);
	const [status, setStatus] = useState({
		cond: 'pending',
		indices: [],
		critical:[],
		winner:[],
		loser:[]
	});
	const saveGame = (opp, outcome, moves) => {
		console.log({ opp, outcome, moves });
		socket.emit('save', {
			user: props.user,
			opponent: opp,
			result: outcome,
			moves: moves
		});
		return;
	};const reset = (flag) => {
		if (flag) {
			socket.emit("closereq", true);
			socket.on("closereqACK", (flag) => {
				if (flag) {
					socket.removeAllListeners("closereqACK");
					setConnected({ ...status,status: 'disconnected',details:{}});
				}
			})
		}
		setHistory([]);
		setStatus({ cond: 'pending', indices: [], critical: [], winner: [], loser: [] });
		// Cookies.set('gameHistory',JSON.stringify({history:[]}));
		return;
	};
	const disconnect = () => {
		socket.emit('leave', {roomns:isConnected.roomns,publicId:socket.publicID});
		socket.on('left',(flag)=>{
			if(flag){
				setConnected({status:'disconnected',roomns:null,details:null});
				socket.removeAllListeners('left');
			}
		});
		reset();
	};
	useEffect(()=>{
		let nhis = new Set([...history]);
		if(Array.from(nhis)!=history){
			Cookies.set('gameHistory',JSON.stringify({history:Array.from(nhis)}),{
				expires:24*60*60
			});
		}
	},[history]);
	useEffect(() => {
		if (!isConnected.status) {
			Cookies.set('connectStatus', 'disconnected');
		}
		else {
			Cookies.set('connectStatus', isConnected.status);
		}
	}, [isConnected.status]);
	useEffect(() => {
		Cookies.set('gameDetails',JSON.stringify(isConnected.details),{
			expires:24*60*60
		});
	}, [isConnected]);
	useEffect(() => {
		Cookies.set('socket.roomid',isConnected.roomns);
		if (isConnected.roomns) {
			socket.emit("join", isConnected.roomns);
			// Verify that server instance joined the room
		}
	}, [isConnected.roomns]);
	useEffect(() => {
		if (isConnected.status === 'connecting' && timer > 0) {
			setTimeout(() => {
				setTimer(timer - REQ_TIMEOUT / 100);
			}, 1000 * REQ_TIMEOUT / 100);
		}
		else if (isConnected.status === 'connecting' && timer <= 0) {
			setConnected({ ...isConnected, status: 'disconnected' });
		}
		Cookies.set('connectTimer', timer);
	}, [timer]);
	
	const fetchroom = useCallback(async () => {
		try {
			let data = await axios.post("https://sock8-backend.onrender.com/play/getroom", {
				data: {
					game: props.game, sid: socket.publicID, ...props.user
				}
			});
			let payload = data.data;
			window.alert(`=======>${payload.msg}`);
			switch (payload.msg) {
				case 'Request pending':
					socket.on("Request processed", (result) => {
						if (result.status === 'failed') {
							window.alert(`Couldn't connect you ...`);
							setConnected({ ...isConnected, status: 'disconnected' });
						}
						else if (result.status == 'matched') {
							socket.removeAllListeners("Request processed");
							Cookies.set("socket.roomid",result.namespace);
							setConnected({ status: 'connected', roomns: result.namespace,details:result.details });
							console.log(result.namespace);
							setTimer(0);
						}
					});
					setConnected({ ...isConnected, status: 'connecting' });
					setTimer(REQ_TIMEOUT);
					break;
				case 'Room joined':
					setConnected({ status: 'connected', roomns: payload.namespace,details:payload.details });
					console.log(payload.details);
					break;
			}
		}
		catch (err) {
			console.error(err);
		}
	}, [props.user]);
	useEffect(() => {
		const sessionID = localStorage.getItem("sessionID");
		if (sessionID) {
			socket.auth = { sessionID };
		}
		socket.on("session", ({ sessionID, publicID }) => {
			socket.auth = { sessionID };
			localStorage.setItem("sessionID", sessionID);
			socket.publicID = publicID;
		});
		socket.on("sdfs", (msg) => {
			window.alert("Move");
		});
		socket.connect();
		socket.emit("join", isConnected.roomns);
		socket.emit('dsfs', 'df');
		if(history.length>0){
			setStatus({...status,indices:history});
		}
		return () => {
			Cookies.set('gameDetails',JSON.stringify(isConnected.details),{
				expires:24*60*60
			});
			Cookies.set('gameHistory',JSON.stringify({history}),{
				expires:24*60*60
			});
			// window.alert(`${JSON.stringify(isConnected.details)}`);
		}
		// saveGame(1,'win',[0,2,1,4,3]);
	}, []);
	switch (isConnected.status) {
		// localhost:5000 change to server url later
		case 'disconnected':
			return (
				<div className="gamecard">
					<Card variant="elevation" sx={{ display:"flex",justifyContent:"space-between",alignItems:"center",backgroundColor: "#B001DF43",boxShadow: `0 0 .2rem #fff,0 0 .2rem #fff,0 0 2rem #B001DF,0 0 0.8rem #B001DF,0 0 2.8rem #B001DF,inset 0 0 1.3rem #B001DF`, color:"white",fontSize: "2em" }}>
						<CardMedia component="img" src="./game1.PNG" sx={{width:"45%",height:"80%",display:"grid",placeItems:"center"}}/>
						<CardContent  style={{padding:"1em",height:"100%",width:"50%",border:"2px solid #272727"}}>
							<Typography gutterBottom variant="headline" component="h2">
								Rules
							</Typography>
							<Typography component="p">
								<ul style={{display:"flex",flexDirection:"column",justifyContent:"space-between"}}>
									<li>You get assigned either an X or O</li>
									<li>The goal of the game is to get a fill either a row,column or diagonal with the shape assigned to you</li>
									<li>The player to first achieve the goal wins the game</li>
									<li>In case no vacant square remains in the grid, a draw is declared</li>
								</ul>
							</Typography>
						</CardContent>
					</Card>
					<div className="gamecontrol" style={{background:"#27272733"}}>
						<Button onClick={fetchroom} variant={'contained'} sx={{ background: 'linear-gradient(to right,#808000,#008000a4),repeating-linear-gradient(30deg,#e6646500, #e6646500 20px, #808000 20px, #808000 22px),repeating-linear-gradient(-45deg,#e6646500, #e6646500 20px, #808000 20px, #808000 22px)', backgroundPositionX: "0px,0px,20px,30px", height: '97.5%', aspectRatio: 3.35, textWrap: "nowrap", fontSize: '1.6em', color: "#ddd", fontFamily: "system-ui", border: "2px solid #ffffff64" }}><Play />Start Game</Button>
						<Select labelId="label" id="nplayers" value="1" sx={{ background: "#ffffff04", color: "white", border: "2px solid white", height: "97.5%", width: "max(100px,10%)", fontSize: "2rem", textAlign: "center", fontFamily: "system-ui" }}>
							<MenuItem value="1">1</MenuItem>
							<MenuItem value="2">2</MenuItem>
						</Select>
					</div>
				</div>
			); break;
		case 'connecting':
			return (
				<Card id="connecting" variant="outlined" sx={{ placeSelf: "start center", border: '.15rem solid white', boxShadow: `0 0 .2rem #fff,0 0 .2rem #fff,0 0 2rem #ddd,0 0 0.8rem #ddd,0 0 2.8rem #ddd,inset 0 0 1.3rem #ddd`, textShadow: '5px 5px 16px #ddd', color: '#ddd', backgroundSize: '150%', padding: '1rem', fontSize: '3rem', display: 'flex', flexDirection: 'column', justifyContent: 'space-around', alignItems: "center", height: "max(60vh,450px)", aspectRatio: '1.1' }}>
					<IconButton onClick={reset.bind(null, true)} sx={{ position: 'absolute', borderRadius: '50%', aspectRatio: 1, width: '60px', transform: 'translate(350%,-120%)' }}><CloseIcon /></IconButton>
					<strong style={{}}>{'Connecting you ...'}</strong>
					<quote style={{ fontSize: '2rem' }}>{'Do not refresh the page'}</quote>
					<LinearProgress variant="determinate" value={(1 - (timer / REQ_TIMEOUT)) * 100} sx={{ borderRadius: '2em', border: '2px solid white', boxShadow: `0 0 .2rem #fff,0 0 .2rem #fff,0 0 2rem #272727,0 0 0.8rem #272727,0 0 2.8rem #272727,inset 0 0 1.3rem #272727`, height: 50, width: '95%', backgroundColor: 'transparent', '& .MuiLinearProgress-bar': { borderTopRightRadius: "2em", borderBottomRightRadius: "2em", backgroundColor: '#ddd', boxShadow: `0 0 .2rem #fff,0 0 .2rem #fff,0 0 2rem #ddd,0 0 0.8rem #ddd,0 0 2.8rem #ddd,inset 0 0 1.3rem #ddd` } }}>Matching ...</LinearProgress>
				</Card>
			); break;
		case 'connected':
			return (
				<div
					className="GameBox"
					style={{
						aspectRatio: props.orientation === "vertical" ? "3/4" : "4/3",
						height: props.orientation === "vertical" ? "auto" : "25vh",
						width: props.orientation === "vertical" ? "25vh" : "auto",
					}}>
					<div className="handle">
						<IconButton sx={{ borderRadius: '50%', aspectRatio: 1, fontSize: "50%" }} onClick={reset}><ReloadIcon /></IconButton>
						<IconButton sx={{ borderRadius: '50%', aspectRatio: 1, fontSize: "50%" }} onClick={disconnect}><CloseIcon /></IconButton>
					</div>
					{
						[1].map((e)=>{
							return createElement(gc[props.game].component,{status,setStatus,saveG:saveGame,config:props.config,details:isConnected.details,user:props.user,key:1,setConnected,isConnected,setHistory,history,initMess:props.initMess})
						})
					}
				</div>
			);
			break;
	}
}
